@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.light{
  --violet: #572EE5;
  --violet-white: #572EE5;
  --green: rgb(79,227,193);
  --grey: #E7E7E7;
  --grey-black: #E7E7E7;
  --black-white: #121212;
  --white-black:  white;
}


.dark{
  --violet: #572EE5;
  --violet-white: #FFFFFF;
  --green: rgb(79,227,193);
  --grey: #E7E7E7;
  --grey-black: #212121;
  --black-white: #FFFFFF;
  --white-black: rgb(55,55,55);
}

*{
  margin: 0;
  padding: 0;
}

.App{
  border-top: 5px solid var(--violet);
  background: var(--white-black);
}

.flex-row{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.flex-column{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.center{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.principal-container{
  width: 900px;
  border: 1px solid var(--grey);
  font-weight: 700;
}

.violet-text{
  font-family: 'Roboto', sans-serif;
  color: var(--violet-white);
}


.nav{
    margin: 7% 0;
}

.principal-title{
    font-size: 36px;
    line-height: 42px;
}
.search-close{
    border: 1px solid var(--black-white);
    border-radius: 61px;
    border-right: none;
    width: 542px;
    padding-left: 2.5%;
}

.search-open{
    border: 1px solid var(--black-white);
    border-radius: 25px;
    width: 542px;
    padding-left: 2.5%;
}

.container-searched{
    -webkit-flex: 1 1;
            flex: 1 1;
}

.input-search{
    font-size: 18px;
    border: none;
    width: 100%;
    height: 52px;
    background: none;
    color: var(--black-white);
    outline: none;
    padding-right: 17.5%;
    padding-left: 6%;
    -webkit-transform: translateX(-22px);
            transform: translateX(-22px);
    border-radius: 61px;
}

.first-input{
    padding-right: 3%;
    padding-left: 1%;
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
    border-bottom: 1px solid var(--grey);
    border-radius: 0;
}

.btn-search{
    width: 86px;
    padding: 2.5%;
    background: var(--violet);
    border-left: 1px solid var(--black-white);
    border-radius: 0 61px 61px 0;
    -webkit-transform: translateX(0.5px);
            transform: translateX(0.5px);
    border-right: 0;
    border-top: 0;
    border-bottom: 0;
    height: 52px;
}

.btn-open{
    border-radius: 0 23px 0px 0;
}

.btn-search:hover{
    background: var(--green);
    cursor: pointer;
}

.icon-search{
    width: 100%;
    height: 100%;
    background: none;
}

.autocomplete {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;

}

.no-autocomplete{
  border: none;
}

.autocomplete-items {
  z-index: 9999;
  background: var(--white-black);
}
  
.autocomplete-items:hover {
  background: var(--grey); 
  cursor: pointer;
}
.cards-container{
    background: var(--grey-black);
    width: 100%;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    margin: 2% 0;
    padding: 3% 0;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.secundary-title {
    font-size: 24px;
    line-height: 28px;
    font-weight: 400;
}
.card{
    width: 250px;
    height: 250px;
    margin: 1.5% 0;
    border: 3px solid var(--white-black);
    border-radius: 5px;
}
.notification{
    font-size: 20px;
    border: 3px solid var(--green);
    border-radius: 15px;
    padding: 6%;
}
.header{
    padding: 1% 7%;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.logo{
    Width: 59.11px;
    Height: 59.11px
}
.btn{
    border: 1px solid var(--violet-white);
    background: var(--white-black);
    box-sizing: border-box;
    border-radius: 61px;
    padding: 1% 2%;
    font-weight: 700;
}

.btn:hover{
    
    cursor: pointer;
    color: var(--green);
}
.loading {
    width: 100%;
}

.img-loading{
    height: 190px;
    background: none;

}
